  constructor() {
    this.selected_work = this.portfolio ;
    this.current_img = 0 ;
  }

  ngOnInit() {
    $(".activeItem").removeClass('activeItem') ;
    $(".workSidenav").addClass('activeItem') ;
  }


  OnCloseSlider() {
    $(".img_viewer").fadeOut(500) ;
    this.current_img = 0 ;
    $(".image_bg , .one_image img").fadeOut(500) ;
    $(".all_work_space").fadeIn(0) ;
    $("video").trigger('pause') ;
    this.selected_work = this.portfolio ;
  }

  OnViewWork(selected_work) {
    this.selected_work = selected_work ;
    this.current_img = 0 ;
    $(".all_work_space").fadeOut(0) ;
    $(".img_viewer").fadeIn(500) ;
    setTimeout(()=>{
      $(".one_image img").fadeIn(500) ;
      $(".image_bg").fadeIn(1000) ;
    } , 500) ;

    for(let i = 0 ; i < this.selected_work.imgs.length ; i++) {
      $("#i"+i).css('left' , i*100+'%'  );
    }
    $(".one_img_selected").css('left' , '0') ;
  }

  OnClickBord(index) {

    $(".image_bg").fadeOut(0) ;
    $(".one_img_selected").animate({'left' : $(".one_img_selected").width()*(index)+'px'} , 500 , function(){}) ;
    let time_round = 200 * Math.abs(index - this.current_img) ;
    if ( index < this.current_img) {
      for (let i = this.current_img ; i >= index ; i--) {
        $("#i"+i).animate({'left' : (i - index )*100+'%'} , 200 , function(){});
      }
    }else {
      for (let i = this.current_img ; i <= index ; i++) {
        $("#i"+i).animate({'left' : (i - index )*100+'%'} , 200 , function(){});
      }
    }


    setTimeout(()=>{
      this.current_img = index ;
      for(let j = 0 ; j < this.selected_work.imgs.length ; j++) {
        $("#i"+j).css('left' , (j - this.current_img)*100+'%'  );
      }

      $(".image_bg").fadeIn(500) ;
    } , time_round) ;

  }

  OnSlideRight() {
    if(this.current_img+1 < this.selected_work.imgs.length)  {
      $("video").trigger('pause') ;
      $(".image_bg").fadeOut(500) ;
      setTimeout(()=>{
        $(".one_img_selected").animate({'left' : $(".one_img_selected").width()*(this.current_img+1)+'px'} , 300 , function(){}) ;
        $("#i"+this.current_img).animate({'left' : '-100%'} , 600 , function(){});
        $("#i"+(this.current_img+1)).animate({'left' : '0%'} , 600 , function(){
          $(".image_bg").fadeIn(500) ;
        });
        for(let i = 0 ; i < this.selected_work.imgs.length ; i++) {
          if (i == this.current_img || i == (this.current_img+1)) {
            continue ;
          }
          $("#i"+i).css('left' , (i - this.current_img - 1)*100+'%'  );
        }
        this.current_img++ ;

      } , 500) ;
    }
  }

  OnSlideLeft() {
    if(this.current_img > 0 )  {
      $("video").trigger('pause') ;
      $(".image_bg").fadeOut(500) ;
      setTimeout(()=>{
        $(".one_img_selected").animate({'left' : $(".one_img_selected").width()*(this.current_img-1)+'px'} , 300 , function(){}) ;
        $("#i"+this.current_img).animate({'left' : '100%'} , 600 , function(){});
        $("#i"+(this.current_img-1)).animate({'left' : '0%'} , 600 , function(){
          $(".image_bg").fadeIn(500) ;
        });
        for(let i = 0 ; i < this.selected_work.imgs.length ; i++) {
          if (i == this.current_img || i == (this.current_img-1)) {
            continue ;
          }
          $("#i"+i).css('left' , (i - this.current_img + 1)*100+'%'  );
        }
        this.current_img-- ;

      } , 500) ;
    }
  }



