  showPage() {
    let timer = 300 ;
      if ($(window).width() < 768 ) {
        timer = 0 ;
      }

      $(".fake_cards").fadeIn(0) ;


      for (let index = 1; index < 17; index++) {
        $(".card_f.card"+index).animate({
          'top' : ($(".card_p.card"+index).offset()).top+'px' ,
          'left' : ($(".card_p.card"+index).offset()).left+'px'
        } , timer , function(){
            $(window).resize(()=> {
              if ($(".fake_cards") != undefined) {
                $(".card_f.card"+index).css({ 'top' : ($(".card_p.card"+index).offset()).top+'px' , 'left' : ($(".card_p.card"+index).offset()).left+'px'  }) ;
              }
            });
          }) ;

      }
      setTimeout(() => {
        $(".card_p").css("opacity" , "1") ;
        $(".fake_cards").fadeOut(0) ;
        $(".card_r").ready(()=> {
          $(".card_r").css('transform' , 'rotateY(180deg)') ;
          $(".card12 .one_card").mouseenter(()=>{
            $(".card12").css('transform' , 'rotateY(180deg)' ) ;
          }) ;

          $(".card12").mouseleave(()=>{
            $(".card12").css('transform' , 'rotateY(0deg)' ) ;
          }) ;

          $(".card9 .one_card").mouseenter(()=>{
            $(".card9").css('transform' , 'rotateY(180deg)' ) ;
          }) ;

          $(".card9").mouseleave(()=>{
            $(".card9").css('transform' , 'rotateY(0deg)' ) ;
          }) ;

          $(".one_card_background").fadeIn(500) ;
        });


      } , timer);
  }

  hidePage() {
      $(".card_r").css('transform' , 'rotateY(0deg)') ;
      setTimeout(() => {
        $(".card_p").animate({"opacity" : "0"} , 500 , function () {
          $(".fake_cards .one_card_div").css({"bottom" : '-400px' , "left" : "20px"}) ;
        }) ;
      }, 1000);
  }
